@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    height: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
  
body {
    font-family: 'Roboto', sans-serif;
    background: #43C6AC;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #191654, #43C6AC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */    
    height: 100%;
}
  
a {
    text-decoration: none;
}
  
#root {
    height: 100%;
}



  

.card {
    display: flex-column;
    background: white;
    width: 300px;
    box-shadow: 7px 10px 12px -5px rgba(131, 26, 26, 0.56);
    padding: 10px;
    margin: 20px;
    border-radius: 15px;
    font-weight: 500;
}
  
.card-details { 
    color: black;
    display: flex;
}
  
.card-name {
    font-size: 15px;
    text-align: left;
    width: 50%;
}

.card-category {
    display: flex;
}
  
 
.card:hover {
    transform: scale(1.05);
}
  
.Templates-info {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    text-align: center;
    flex-grow: 0;
    flex-shrink: 5;
    flex-basis: calc(20% - 20px);
}
  
.Loading-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    color: white;
    margin: 50px 0px 0px 0px;
}

.Loading-screen-text {
    margin-left: 20px;
    font-family: 'Ubuntu Condensed', sans-serif;
}
  
.FilterTool {
    width: 20%;
    display: flex;
    margin-left: 1.8em;
    color: white;
}

.search-form {
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.search-bar {
    width: 50%;
    border: none;
    padding: 10px;
}
  
.search-button {
    background: rgba(13, 49, 170, 0.829);
    font-size: 16px;
    font-weight: 500;
    border: none;
    padding: 10px 20px;
    color: white;
    cursor: pointer;
    font-family: inherit;
}
.pag {
    display: flex;
    flex-direction: row-reverse;
    margin-right: 1.5em;
  }
  
  .pag-li {
    padding: 10px;
    font-size: 18px;
    display: inline-block;
    max-width: 50%; /* limit width */
    word-break: break-all; /* wrap extended text*/
  }
  
  .pag-a {
    color: white;
    text-decoration: none;
  }
  
  a:hover {
    color: #80c565;
  }
  
  .pag-ul {
    list-style: none;
  }
.form-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 30px;
}

@media screen and (max-width: 800px) {
    .form-wrapper {
        flex-direction: column;
        justify-content: center;
    }
}

.FilterTool {
    width: 100%;
    display: flex;
    margin: 20px;
}

.form-select {
    border: none;
    margin-left: 20px;
    font-family: inherit;
    font-size: 17px;
    font-weight: 300;
}

.filter-select {
    margin-left: 20px;
    font-family: inherit;
    font-size: 17px;
    font-weight: 300;
}

.label {
    font-weight: 500;
}
